import {Divider, Flex, Text} from '@chakra-ui/react'
// import { useState } from 'react';

function SkillsCard(props) {

    // TODO: Handle single skill case. Filter fails when it is no iterable.

    const skills = props.categories.map((category) => (

            <Flex
                flexDir="column"
                alignItems="center"
                w="80%"
                flexGrow={1}
                justifyContent="flex-start"
                key={category}
            >
                <Text fontSize="sm">{category}</Text>

                <Flex 
                    flexWrap="wrap"
                    w="100%"
                    justifyContent="space-around"
                    flexGrow={1}
                >
                    {
                        props.children.filter(
                            child => child.props.expertise === category
                        )
                    }
                </Flex>

            </Flex>
    ))
    
    return(
        <Flex
            m={3}
            p={3}
            boxShadow="dark-lg"
            rounded="md"
            // bgColor="#f2f2f2"
            boxShadow={"inset 0 0 0 3000rem rgba(255,255,255,0.9), 0 0 1em black"}
            w = "14rem"
            h = "21rem"
            flexDir="column"
            alignItems="center"
            textAlign="center"
            overflow="auto"
        >
            <Text
                fontWeight="bold"
            >
                {props.title}
            </Text>

            <Divider/>

            <Flex
                flexDir="column"
                alignItems="center"
                w="100%"
                pb={3}
                justifyContent="space-between"
                flexGrow={1}
            >
                {skills}            
            </Flex>            
            


        </Flex>
    )    
}

export default SkillsCard;